body, html {
  width: 100vw;
  height: 100vh;
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  overflow-x: hidden;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

* {
    padding: 0;
    margin: 0;
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
        user-select: none;
}
.header__container {
    background-color: #1B1B1B;
    position: absolute;
    width: 100%;
    height: 100%;
}
.header__ball {
    width: 50px;
    height: 50px;
    border-radius: 99px;
    position: absolute;
    border: 1px solid lightblue;
}


header {
    width: 100vw;
    height: 100vh;
    padding: 0;
    margin: 0;
}

@media screen and (max-width: 1245px) {
    header {
        display: none;
    }
    body {
        background-color: #1B1B1B;
    }
}
.main__board {
    width: 500px;
    height: 500px;
    display: block;
    position: absolute;
    top: 80px;
    left: 33%;
    z-index: 100;
    border-radius: 3px;
    margin:0 auto;
    background-color: #cf5202;
}
.main__gameOver {
    display: -webkit-flex;
    display: flex;
    position: absolute;
    top: 80px;
    left: 33%;
    width: 500px;
    height: 500px;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: center;
            justify-content: center;
    background-color: #cf5202;
    border-radius: 3px;
    margin:0 auto;
}
.gameOver__message {
    width: 200px;
    font-family: 'Ubuntu Mono', monospace;
    text-align: center;
    color: #061429;
}

@media screen and (max-width: 1245px) {
    .main__board {
        position: unset;
        margin: 20px auto;
    }
    .main__gameOver {
        position: unset;
        margin: 20px auto;
    }
}

.main__gridcell {
    float: left;
    width: 25px;
    height: 25px;
    outline: 1px solid #175b59;
}
.main__gridcell--head {
    background-color: #06182e;
}
.main__gridcell--tail {
    background-color: #195bb0;
}
.main__gridcell--food {
    background-color: #114f4d;
}



